import React, { Component } from 'react';
import API from "../../utils/API";
import List from '../List/List'
import ListItem from '../List/ListItem'




class Articles extends Component {


    state = {
        articles : [],
        articles1 : []
      };

        componentDidMount() {


          API.getCurrent()
          .then(res => {
            res = res.data;
            console.log(res.data);
            this.setState({ articles: res.data });
          })
          .catch(err => console.log(err));

          API.getUpcoming()
          .then(res1 => {
            res1 = res1.data;
            console.log(res1.data);
            this.setState({ articles1: res1.data });
          })
          .catch(err => console.log(err));



        }


    render() {
        return(
            <div className="textdiv row">
            <div className="col-md-6 current">
            <h1> Current Events!</h1>
            <span id="json"></span>
            <List>
                {this.state.articles.map(articles => (
                  <ListItem key={articles.title}>
                    <a href={"/books/" + articles.title}>
                      <strong>
                        {articles.link} by {articles.banner} also {articles.icon}
                      </strong>
                    </a>
                  
                  </ListItem>
                ))}
              </List>
            </div>
            <div className = "col-md-6 upcoming">
            <h1> Upcoming!</h1>
            <span id="json1"></span>
            
            
            
            
            </div>
          </div>
        )
    };
};

export default Articles


